.modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  padding: 24px;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.2);

  &.is-hidden {
    opacity: 0;
    pointer-events: none;
  }

  .pretitle {
    text-align: center;
  }

  .title {
    margin-top: 15px;
    text-align: center;
  }
}

.modal__window {
  position: relative;
  margin: auto;
  padding: 40px;
  max-width: 422px;
  border-radius: 8px;
  background-color: $modal-bg;

  @media (min-width: 1200px) {
    max-width: 517px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 23px;
  fill: $title-color;
  background-color: transparent;
  border-color: transparent;

  @media (min-width: 768px) {
    top: 15px;
    right: 15px;
  }
}

.modal__form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 30px;
}

.modal__input {
  padding: 20px 30px;
  width: 100%;
  background-color: $modal-input-bg;
  border: unset;
  border-radius: 5px;

  &::placeholder {
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    color: $modal-input-placeholder;
  }
}

.modal__textarea {
  @extend .modal__input;
  min-height: 150px;
  resize: none;
}

.modal__btn {
  align-self: center;
  margin-top: 30px;
  padding: 15px 35px;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 700;
  color: $button-text-color;
  background-color: $accent-color;
  border-color: transparent;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
}
